.App {
  text-align: center;
  /* font-family: "Segoe UI", "Segoe UI Web (West European)", "Segoe UI Symbol",
    -apple-system, BlinkMacSystemFont, "Helvetica Neue", sans-serif; */
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Custom Scrollbar */
.scrollable-city-list::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

.scrollable-city-list::-webkit-scrollbar-track {
  background: transparent;
  border-radius: 60px;
}

.scrollable-city-list::-webkit-scrollbar-thumb {
  background-color: rgba(209, 218, 231, 0.6);
  border-radius: 60px;
}

.scrollable-city-list::-webkit-scrollbar-thumb:hover {
  background-color: rgba(209, 218, 231, 0.9);
}

.scrollable-city-list {
  scrollbar-width: thin;
  scrollbar-color: rgba(209, 218, 231, 0.6) transparent;
}

